// src/js/index.js
import { fetchImages } from './js/pixabay_api.js'; // üì¶ API mod√ºl√ºn√º √ßaƒüƒ±r
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

// Diƒüer DOM se√ßimleri...
const searchInput = document.querySelector('.search-input');
const searchBtn = document.querySelector('.search-button');
const resultsContainer = document.querySelector('#results-container');
const loader = document.getElementById("loader");

searchBtn.addEventListener('click', async () => {
  const query = searchInput.value.trim();
  if (!query) return;

  loader.style.display="block";

  try {

    const data = await fetchImages(query); 
    await new Promise(resolve => setTimeout(resolve, 1500));
    resultsContainer.innerHTML = "";

    if (data.hits.length === 0) {
      iziToast.warning({
        title: "Uyarƒ±",
        message: "Sorry, there are no images matching your search query. Please try again!",
        position: "topRight"
      });
      return;
    }

    data.hits.forEach(hit => {
      const link = document.createElement("a");
      link.href = hit.largeImageURL;
      link.className = "image-card";

      const captionText = `
        <strong>Tags:</strong> ${hit.tags}<br>
        üëç ${hit.likes} &nbsp;&nbsp;
        üëÅÔ∏è ${hit.views} &nbsp;&nbsp;
        üí¨ ${hit.comments} &nbsp;&nbsp;
        ‚¨áÔ∏è ${hit.downloads}
      `;

      link.setAttribute("data-title", captionText);
      link.setAttribute("data-lightbox", "gallery");

      const img = document.createElement("img");
      img.src = hit.webformatURL;
      img.alt = hit.tags;

      const info = document.createElement("div");
      info.className = "image-info";

      const tags = document.createElement("p");
      tags.className = "tags";
      tags.textContent = `Tags: ${hit.tags}`;

      const stats = document.createElement("div");
      stats.className = "stats";
      stats.innerHTML = `
        <span>üëç ${hit.likes}</span>
        <span>üëÅÔ∏è ${hit.views}</span>
        <span>üí¨ ${hit.comments}</span>
        <span>‚¨áÔ∏è ${hit.downloads}</span>
      `;

      info.appendChild(tags);
      info.appendChild(stats);
      link.appendChild(img);
      link.appendChild(info);

      resultsContainer.appendChild(link);
    });

    let lightbox = new SimpleLightbox('.results-container a', {
      captionsData: 'data-title',
      captionDelay: 250,
      captionPosition: 'bottom',
    });
    lightbox.refresh();

  } catch (error) {
    iziToast.error({
      title: "Hata",
      message: "Bir sorun olu≈ütu. L√ºtfen daha sonra tekrar deneyin.",
      position: "topRight"
    });
  } finally {
    loader.style.display = "none";
  }
});
